      <p>Nacida en Matsumoto (Nagano) en una familia de clase media alta de comerciantes de semillas, Kusama comenzó a crear arte desde una edad muy temprana, lo que la llevó a estudiar, Nihonga(pinturas de estilo japonés) en Kyoto en 1948. Frustrada con este estilo japonés, ella se interesó en el avant-garde americano y europeo, montando varias exhibiciones solistas de sus pinturas en Matsumoto y Tokio durante la década de 1950. En 1957 se mudó a los Estados Unidos, estableciéndose en la ciudad de Nueva York donde produjo una serie de pinturas influenciadas por el expresionismo abstracto. Kusama cambió a la escultura y la instalación como sus medios principales y se convirtió un elemento fijo del avant-garde de Nueva York con sus trabajos expuestos al lado de Andy Warhol, Claes Oldenburg y George Segal a principios de la década de 1960 cuándo la artista se asoció al movimiento del Arte pop. Acogiendo el ascenso de la contracultura hippie de finales de los años sesenta, Kusama llamó la atención del público cuando organizó una serie de happenings en los cuales se pintaba a participantes desnudos con lunares de colores brillantes.
          En 1975, Kusama se mudó de vuelta a Japón, donde ella encontró una escena del arte mucho más conservadora que la de Nueva York. Ella se convirtió en marchante de arte pero su negocio fracasó años después; más adelante padeció de problemas psiquiátricos y en 1977 se internó voluntariamente en un hospital, donde ella ha vivido el resto de su vida. Desde entonces, ella continúa produciendo obras de arte con diferentes medios.</p>
