olores brillantes. En 1975, Kusama se mudó de vuelta a Japón, donde ella encontró una escena del arte mucho más conservadora que la de Nueva York. Ella se convirtió en marchante de arte pero su negocio fracasó años después; más adelante padeció de problemas psiquiátricos y en 1977 se internó voluntariamente en un hospital, donde ella ha vivido el resto de su vida. Desde entonces, ella continúa produciendo obras de arte con diferentes medios.")]),_c('b-card-group',{attrs:{"columns":""}},[_c('b-card',{attrs:{"img-src":"https://raw.githubusercontent.com/mariacampos7/contempart2/main/fotos%20buenas%20prac24%2B/OBRAS%20YK/yk-o1.png","img-alt":"Card image","img-top":"","title":"Retrospective","sub-title":"2021"}}),_c('b-card',{attrs:{"img-src":"https://raw.githubusercontent.com/mariacampos7/contempart2/main/fotos%20buenas%20prac24%2B/OBRAS%20YK/yk-o2.png","img-alt":"Card image","img-top":"","title":"Totum Revolutum","sub-title":"2018"}}),_c('b-card',{attrs:{"img-src":"https://raw.githubusercontent.com/mariacampos7/contempart2/main/fotos%20buenas%20prac24%2B/OBRAS%20YK/yk-o3.png","img-alt":"Card image","img-top":"","title":"La Grande Dame","sub-title":"2012"}})],1),_c('b-container',{staticClass:"p-3 bg-transparent",attrs:{"fluid":""}}),_c('Slider2'),_c('b-container',{staticClass:"p-3 bg-transparent",attrs:{"fluid":""}})],1)}
