<template>
  <div class="tienda">
    <div>
       <b-card
         overlay
         img-src="https://raw.githubusercontent.com/mariacampos7/contempart2/main/fotos%20buenas%20prac24%2B/FOTOS%2BTEXTOS/p-tienda2.png"
         text-variant="dark"
         title="TIENDA"> </b-card>
    </div>
    <b-container fluid class="p-3 bg-transparent">
    </b-container>
    <b-container fluid class="p-2 bg-white">
     <div class="clearfix">
      <b-img right src="https://raw.githubusercontent.com/mariacampos7/contempart2/main/fotos%20buenas%20prac24%2B/TIENDA/carritofinal.png" alt="Right image"></b-img>
     </div>
    </b-container>
    <b-container fluid class="p-3 bg-transparent">
    </b-container>
    <div>
    <!-- <div aling-v="center"> -->
      <div class="container-md">
          <b-card-group columns>
            <div columns v-for="proyecto in proyectos" :key="proyecto.id">
             <Card2 :titol="proyecto.campo1" :texto="proyecto.campo2" :url="proyecto.url" :srcimg="proyecto.srcimg"></Card2> 
            </div>
           </b-card-group>
       </div>
       <b-container fluid class="p-3 bg-transparent">
       </b-container>
        <Pagination/>
       </div>
      </div>
     </template>

<script>
import Card2 from '../components/Card2.vue'
import Pagination from '../components/Pagination.vue'
const lstProysJson = '../exemple.json';

export default {
  props: {
   name: {
      type: String,
      default: 'Pagina2'
    }
  },
  components: {
        Card2,
        Pagination
      },
  data() {
      return {
        proyectos:[],
        githubPage: 1,
        page: 1,
        loading: false,
        perPage: 5
      }
    },
    mounted() {
      this.fetchData()
    },

    methods: {
        async fetchData() {
          const res = await fetch (lstProysJson);
          const val= await res.json();
          this.proyectos=val;
          console.log(val);
        }
    }
  }
</script>