     <p>Tras una exitosa carrera como ilustrador profesional, Warhol adquirió fama mundial por su trabajo en pintura, cine de vanguardia y literatura, notoriedad que vino respaldada por una hábil relación con los medios y por su rol como gurú de la modernidad. Warhol actuó como enlace entre artistas e intelectuales, pero también entre aristócratas, homosexuales, celebridades de Hollywood, drogadictos, modelos, bohemios y pintorescos personajes urbanos.
         Warhol utilizó medios diferentes para crear sus obras, como el dibujo a mano, la pintura, el grabado, la fotografía, la serigrafía, la escultura, el cine y la música. El Museo Andy Warhol en su ciudad natal, Pittsburgh, Pensilvania, contiene una amplia colección permanente de arte. Resulta ser el museo más grande de Estados Unidos dedicado a un solo artista.
         Uno de los aportes más populares de Warhol fue su declaración: «En el futuro todo el mundo será famoso durante 15 minutos». Esta frase de cierta manera vaticinó el actual poder de los medios de comunicación y el apogeo de la prensa amarilla, los reality shows y las redes sociales.
         Fue un personaje polémico durante su vida —algunos críticos calificaban sus obras como pretenciosas o bromas pesadas— y desde su muerte en 1987 es objeto de numerosas exposiciones retrospectivas, análisis, libros y documentales, además de ser recreado en obras de ficción como la película I Shot Andy Warhol (Mary Harron, 1996). Al margen de la fama y de la polémica, está considerado como uno de los artistas más influyentes del siglo xx debido a sus revolucionarias obras.</p>
