da —algunos críticos calificaban sus obras como pretenciosas o bromas pesadas— y desde su muerte en 1987 es objeto de numerosas exposiciones retrospectivas, análisis, libros y documentales, además de ser recreado en obras de ficción como la película I Shot Andy Warhol (Mary Harron, 1996). Al margen de la fama y de la polémica, está considerado como uno de los artistas más influyentes del siglo xx debido a sus revolucionarias obras.")]),_c('b-card-group',{attrs:{"columns":""}},[_c('b-card',{attrs:{"img-src":"https://raw.githubusercontent.com/mariacampos7/contempart2/main/fotos%20buenas%20prac24%2B/OBRAS%20AW/aw-o1.png","img-alt":"Card image","img-top":"","title":"Marilyn Monroe"}}),_c('b-card',{attrs:{"img-src":"https://raw.githubusercontent.com/mariacampos7/contempart2/main/fotos%20buenas%20prac24%2B/OBRAS%20AW/aw-o2.png","img-alt":"Card image","img-top":"","title":"Nature"}}),_c('b-card',{attrs:{"img-src":"https://raw.githubusercontent.com/mariacampos7/contempart2/main/fotos%20buenas%20prac24%2B/OBRAS%20AW/aw-o3.png","img-alt":"Card image","img-top":"","title":"Mick Jagger"}})],1),_c('b-container',{staticClass:"p-3 bg-transparent",attrs:{"fluid":""}}),_c('Slider'),_c('b-container',{staticClass:"p-3 bg-transparent",attrs:{"fluid":""}})],1)}
